import { LitElement, html, css } from "lit";

class MatterMap extends LitElement {
  static get styles() {
    return css`
      :host {
        display: block;
        font-family: Source Sans Pro, sans-serif;

        --main-hover: #0b4c1a;
        --main: #087021;
        --main-help: #c4ddca;
        --main-specific-help: #f7fbf8;
        --main-backgroung: #ffffff;

        --grey-main: #333333;
        --grey-help: #bebebe;
        --grey-container: #eeeeee;
        --grey-specific-help: #f5f5f5;

        --status-success: #2abc67;
        --status-info: #5bc0de;
        --status-warning: #f0ad4e;
        --status-error: #d9534f;

        --graph-1: #77b5c3;
        --graph-2: #4863a3;
        --graph-3: #9371ac;
        --graph-4: #d87d7d;
        --graph-5: #d9a46c;
        --graph-6: #e3cd66;
      }

      .map {
        text-align: center;
      }

      .titulo {
        color: #333333; 
        font-family: "Source Sans Pro"; 
        font-size: 36px; 
        font-weight: bold; 
        letter-spacing: 0;
      }

      .subtitle {
        color: rgb(51, 51, 51); 
        font-family: "Source Sans Pro"; 
        font-size: 16px;
        font-weight: 500; 
        letter-spacing: 0px;
      }

      .bloque-titulo-subtitle {
        margin-bottom: 15px;
      }

    `;
  }

  static get properties() {
    return {
      configuration: { type: String },
      _responseConfiguration: { type: Object },
    };
  }

  constructor() {
    super();
    this.libreriasCargadas = false;
  }

  // Se ejecuta una única vez al comienzo.
  firstUpdated() {
    this.addMapea();
    this.secondUpdated();
  }

  // Cada vez que se actualiza algún parámetro de entrada en la llamada del matter-map y no ha sido la primera vez, entra en esta función.
  secondUpdated() {
    this.updatedResponseConfiguration();
  }

  // Cada vez que se modifique algún parámetro de entrada, entra aquí.
  updated() {

    // En el caso de que ya este creado de antes, se elimina la variable div id"map" y se cargan los datos de nuevo
    if (document.getElementById("map") !== null) {
      this._responseConfiguration = undefined;

      var elem = document.getElementById("map");
      elem.parentNode.removeChild(elem);

      this.secondUpdated();

      // Si existen datos en responseData, respondeConfiguration y se han cargado los 3, se actualiza el setParametrs
    } else {
      if (this._responseConfiguration !== undefined && this.libreriasCargadas) {
        this.setParametersMapea();
      }
    }
  }



  // En esta función lo único que se hace es parsear el JSON/url que se le introduce de configuracion, metiéndolo en la variable this._responseConfiguracion en forma de objeto.
  updatedResponseConfiguration() {
    const conf = this.configuration.substr(0, 4);

    if (this.configuration !== null && this.configuration !== "undefined") {
      if (conf === "http") {
        fetch(this.configuration).then(response => {
          if (response.ok) {
            response.json().then(json => {
              this._responseConfiguration = json;
            }).catch(error => {
              this._responseConfiguration = [{ error: `ERROR_Json: ${error.message}` }];
            });
          } else {
            this._responseConfiguration = [{ error: `Respuesta de red KO` }];
          }
        }).catch(error => {
          this._responseConfiguration = [{ error: `ERROR_Fetch: ${error.message}` }];
        });
      } else {
        this.jsonSeccionesDetalleConf = JSON.parse(this.configuration);
        this._responseConfiguration = this.jsonSeccionesDetalleConf;
      }
    } else {
      this._responseConfiguration = null;
    }

    this.update();
  }


  // Se añaden las librerias de MAPEA desde dentro del wc.
  addMapea() {

    // "https://emiliopardo.github.io/simplelegend/simplelegend.ol.min.js"


    this.cont = 0;
    // console.log("CSS MAPEA - importing...");
    const mapeaStyles = document.createElement("link");
    mapeaStyles.href = "https://mapea4-sigc.juntadeandalucia.es/assets/css/mapea-6.3.0.ol.min.css";
    mapeaStyles.rel = "stylesheet";
    mapeaStyles.onload = this.contImportLibrerias();
    document.head.append(mapeaStyles);
    console.log("CSS MAPEA - imported");

    // console.log("CSS PLUGIN MAPEA BASELAYER- importing...");
    const mapeaBaseLayerPluginStyles = document.createElement("link");
    mapeaBaseLayerPluginStyles.href = "https://emiliopardo.github.io/simplebaselayerselector/simplebaselayerselector.ol.min.css";
    mapeaBaseLayerPluginStyles.rel = "stylesheet";
    mapeaBaseLayerPluginStyles.onload = this.contImportLibrerias();
    document.head.append(mapeaBaseLayerPluginStyles);
    console.log("CSS PLUGIN MAPEA BASELAYER - imported");

    // console.log("CSS PLUGIN MAPEA SIMPLELEGEND- importing...");
    const mapeaSimpleLegendPluginStyles = document.createElement("link");
    mapeaSimpleLegendPluginStyles.href = "https://emiliopardo.github.io/simplelegend/simplelegend.ol.min.css";
    mapeaSimpleLegendPluginStyles.rel = "stylesheet";
    mapeaSimpleLegendPluginStyles.onload = this.contImportLibrerias();
    document.head.append(mapeaSimpleLegendPluginStyles);
    console.log("CSS PLUGIN MAPEA BASELAYER - imported");

    this.head = document.getElementsByTagName("head")[0];

    // Se mete un delay para que se vayan importando en el orden correcto, sino no funciona. Orden a seguir: 1º: CSS, 2º: MIN, 3º: CONF , 4º LUGIN MAPEA BASELAYER, 5º 
    setTimeout(() => {

      // console.log("MIN MAPEA - importing...");
      var scriptMinMapea = document.createElement('script');
      scriptMinMapea.src = "https://mapea4-sigc.juntadeandalucia.es/js/mapea-6.3.0.ol.min.js";
      scriptMinMapea.type = 'text/javascript';
      scriptMinMapea.onload = this.contImportLibrerias();
      this.head.appendChild(scriptMinMapea);
      console.log("MIN MAPEA - imported");

      setTimeout(() => {
        // console.log("CONF MAPEA - importing ...");
        var scriptConfMapea = document.createElement('script');
        scriptConfMapea.src = "https://mapea4-sigc.juntadeandalucia.es/js/configuration-6.3.0.js";
        scriptConfMapea.type = 'text/javascript';
        scriptConfMapea.onload = this.contImportLibrerias();
        this.head.appendChild(scriptConfMapea);
        console.log("CONF MAPEA - imported");

        setTimeout(() => {
          // console.log("PLUGIN MAPEA BASELAYER- importing...");
          var scriptConfBaseLayerPlugin = document.createElement('script');
          scriptConfBaseLayerPlugin.src = "https://emiliopardo.github.io/simplebaselayerselector/simplebaselayerselector.ol.min.js";
          scriptConfBaseLayerPlugin.type = 'text/javascript';
          scriptConfBaseLayerPlugin.onload = this.contImportLibrerias();
          this.head.appendChild(scriptConfBaseLayerPlugin);
          console.log("PLUGIN MAPEA BASELAYER - imported");
          setTimeout(() => {
            this.updated();
          }, 300);
        }, 300);

      }, 300);
    }, 300);
  }

  // Método que lleva la cuenta de cuantas librerias necesarias de mapea se han importado
  contImportLibrerias() {
    this.cont += 1;
    if (this.cont == 6) {
      this.libreriasCargadas = true;
    }
  }

  // Método donde se crea el elemento div necesario para que funcione MAPEA y la leyenda
  setParametersMapea() {

    // Creamos el elemento div principal, necesario para mostrar el mapa
    const div = document.createElement("div");
    div.id = "map";
    div.style.cssText = 'position: relative; height:600px;';
    var aux_div = document.getElementById('mattermap');
    aux_div.after(div);

    this.setInitialConfigurationMAPEA();
  }

  // Método que inicializa la configuración principal de MAPEA
  setInitialConfigurationMAPEA() {
    let initiallayers = new Array()
    // Se crea la variable mapajs que es la que va a configurar toda la vista de MAPEA con una configuracion inicial ya preestablecida.
    // He puesto para que nada mas iniciar el mapa, salga sevilla, marcando el punto del centro de Sevilla.
    let layers = this._responseConfiguration.configuration.initiallayers
    console.log(layers)
    layers.forEach(element => {
      let legendurl = element.urlLegend
      console.log(legendurl)
      let layer = new M.layer.WMS(element)
      if (legendurl!=="") {
        layer.setLegendURL(legendurl)
      }
      initiallayers.push(layer)
    });
    this.mapajs = window.M.map({
      container: "map",
      layers: initiallayers,
      controls: this._responseConfiguration.configuration.controls,
      center: {
        x: this._responseConfiguration.configuration.initialcenter.coordinates[0],
        y: this._responseConfiguration.configuration.initialcenter.coordinates[1],
      },
      projection: "EPSG:25830*m",
      zoom: this._responseConfiguration.configuration.initialzoom
    });

    // CONFIGURACION DE PLUGINS
    const configSimplebaselayerselector = { displayBaseLayersInLayerSwitcher: false }

    const simplebaselayerselectorPlugin = new M.plugin.Simplebaselayerselector(configSimplebaselayerselector)

    this.mapajs.addPlugin(simplebaselayerselectorPlugin);
  }

  render() {
    return html`
      ${this._responseData !== null && this._responseConfiguration !== undefined ? html`
        <div class="bloque-titulo-subtitle">
          <div class="titulo">${this._responseConfiguration.title}</div>
          <div class="subtitle">${this._responseConfiguration.subtitle}</div>
        </div>
      ` : html``}`;
  }

}

if (!window.customElements.get("matter-map")) {
  window.customElements.define("matter-map", MatterMap);
}
